import React from 'react'
import Person from './Person'

const Persons = ({ persons, removePersonHandler }) => {
    return (
        <div>
            {persons.map(person =>
                <Person
                    key={person.name}
                    person={person}
                    removePersonHandler={removePersonHandler}
                />
            )}
        </div>
    )
}

export default Persons

